:host {
  display: block;
}
.panel-section {
  font-weight: var(--calcite-font-weight-medium);
  font-size: var(--calcite-font-size-0);
  padding: 0.75rem 0.75rem 0.5rem 0.75rem;
}

.panel-action {
  padding: 0.5rem 0.75rem 0.5rem 0.75rem;
}

.block-wrapper {
  border: 1px solid var(--calcite-color-border-1);
}

.filters {
  display: grid;
  gap: 0.5rem;
}

/* .filter {
  border: 1px solid var(--calcite-color-border-1);
} */

.buffer {
  margin: 0.5rem 0px;
}

.buffer-bottom {
  margin-bottom: 1rem;
}

.filter-fields {
  display: grid;
  gap:0.55rem;
}

.list {
  border: 1px solid var(--calcite-color-border-1);
}